import React, { useState, useEffect } from "react";
import { Slider } from "@mui/material";

import { arrIsSorted, bubbleSort, selectionSort, mergeSort, quickSort, insertionSort } from "./sorting";

import "./SortingVisualizer.css";

const randomIntFromInterval = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1) + min);
};

const generateArray = (length) => {
    const arr = [];
    for (let i = 0; i < length; i++) {
        arr.push(randomIntFromInterval(5, 400));
    }
    return arr;
};

const SortingVisualizer = () => {
    const [array, setArray] = useState([]);
    const [currentIndex, setCurrentIndex] = useState([0, 0]);
    const [length, setLength] = useState(50);
    const [speed, setSpeed] = useState(100);
    const [isSorting, setIsSorting] = useState(false);
    const [animationInterval, setAnimationInterval] = useState(null);

    const newArray = () => {
        setIsSorting(false);
        if (animationInterval) clearInterval(animationInterval);
        setArray(generateArray(length));
    };

    useEffect(() => {
        setArray(generateArray(length));
    }, [length]);

    const renderLengthSlider = () => {
        return (
            <div className="slider">
                <div className="lead">Select array size:</div>
                <Slider
                    value={length}
                    min={10}
                    max={100}
                    step={10}
                    valueLabelDisplay="auto"
                    onChange={(e) => setLength(e.target.value)}
                />
            </div>
        );
    };

    const renderSpeedSlider = () => {
        return (
            <div className="slider">
                <div className="lead">Select sorting speed:</div>
                <Slider
                    value={speed}
                    min={5}
                    max={200}
                    step={5}
                    valueLabelDisplay="auto"
                    onChange={(e) => setSpeed(e.target.value)}
                />
            </div>
        );
    };

    const startSorting = (func) => {
        if (isSorting) return;
        if (arrIsSorted(array)) {
            alert("Array is already sorted!");
            return;
        }
        const steps = func([...array], 0, array.length - 1) || [];
        console.log(steps);
        setIsSorting(true);
        renderAnimations(steps);
    };

    const renderSortButtons = () => {
        return (
            <div>
                <button className="btn btn-success btn-lg" onClick={() => newArray()}>
                    {!isSorting ? "New Array" : "Stop Animation and Reset"}
                </button>
                <br />
                {!isSorting ? (
                    <div>
                        <div className="lead mt-3 mb-2">Select sorting algorithm</div>
                        <button className="btn btn-primary" onClick={() => startSorting(bubbleSort)}>
                            Bubble Sort
                        </button>
                        <button
                            className="btn btn-primary"
                            style={{ marginLeft: "20px" }}
                            onClick={() => startSorting(selectionSort)}
                        >
                            Selection Sort
                        </button>
                        <button
                            className="btn btn-primary"
                            style={{ marginLeft: "20px" }}
                            onClick={() => startSorting(mergeSort)}
                        >
                            Merge Sort
                        </button>
                        <button
                            className="btn btn-primary"
                            style={{ marginLeft: "20px" }}
                            onClick={() => startSorting(insertionSort)}
                        >
                            Insertion Sort
                        </button>
                        <button
                            className="btn btn-primary"
                            style={{ marginLeft: "20px" }}
                            onClick={() => startSorting(quickSort)}
                        >
                            Quick Sort
                        </button>
                    </div>
                ) : (
                    <></>
                )}
            </div>
        );
    };

    const renderAnimations = (steps) => {
        let i = steps.length;
        let j = 0;
        console.log(speed);
        const interval = setInterval(() => {
            if (i > 0) {
                const [a, b] = steps[j].step;
                const swap = steps[j].swap;
                if (swap) {
                    const temp = array[a];
                    array[a] = array[b];
                    array[b] = temp;
                }
                setArray([...array]);
                setCurrentIndex([a, b]);
                i--;
                j++;
            } else {
                clearInterval(interval);
                setAnimationInterval(null);
                setIsSorting(false);
            }
        }, 205 - speed);
        setAnimationInterval(interval);
    };

    const displayArray = () => {
        return (
            <div className="array-container">
                {array.map((value, index) => {
                    const styling = {
                        height: `${value}px`,
                        backgroundColor: isSorting && currentIndex.includes(index) ? "black" : "green",
                    };
                    return <div className="array-bar" key={index} style={styling}></div>;
                })}
            </div>
        );
    };

    return (
        <div className="app-container">
            <h1 className="mt-5 mb-5">Sorting Algorithms Visualizer</h1>
            <div className="controls mb-5">
                {!isSorting ? (
                    <div className="leftControls">
                        {renderLengthSlider()}
                        {renderSpeedSlider()}
                    </div>
                ) : (
                    <div className="leftControls lead">Animation In Progress</div>
                )}
                {/* control buttons  */}
                {renderSortButtons()}
            </div>
            {displayArray()}
        </div>
    );
};

export default SortingVisualizer;
