import React from "react";
import axios from "axios";

class TypingApp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      words: [],
      time: 60,
      gameFinished: false,
      input: "",
      currentWord: "",
      isStarted: false,
    };
    this.nextWord = "";
    this.timer = 0;
    this.correct = 0;
    this.incorrect = 0;
    this.wrong = [];
  }

  async componentDidMount() {
    const URL = "https://random-word-api.herokuapp.com/word?number=300";
    const response = await axios.get(URL);
    this.nextWord = response.data[response.data.length - 1];
    response.data.pop();
    this.setState({ words: response.data });
    this.newRandomWord();
  }

  newRandomWord = () => {
    const temp = this.nextWord;
    this.nextWord =
      this.state.words[Math.floor(Math.random() * this.state.words.length)];
    this.setState({
      currentWord: temp,
    });
  };

  decreaseTimer = () => {
    const timeLeft = this.state.time;
    if (timeLeft === 0) {
      this.setState({ gameFinished: true });
      clearInterval(this.timer);
      return;
    }
    this.setState({ time: timeLeft - 1 });
  };

  startTimer = () => {
    this.setState({ isStarted: true });
    if (this.timer === 0 && this.state.time > 0) {
      this.timer = setInterval(this.decreaseTimer, 1000);
    }
  };

  makeGuess = (guess) => {
    if (guess === this.state.currentWord + " ") {
      this.correct += guess.length - 1;
    } else {
      this.incorrect += guess.length - 1;
      this.wrong.push([guess, this.state.currentWord]);
    }
    this.setState({
      input: "",
      words: this.state.words.filter((w) => w !== guess),
    });
    this.newRandomWord();
  };

  handleInput = (e) => {
    const val = e.target.value;
    if (val[val.length - 1] === " ") {
      this.makeGuess(val);
    } else {
      this.setState({ input: val });
    }
  };

  render() {
    if (!this.state.isStarted) {
      return <button onClick={this.startTimer}>Start</button>;
    }
    if (this.state.gameFinished) {
      const wpm = Math.round(this.correct / 5);
      const wrong = this.wrong.map((w) => {
        return (
          <div>
            {w[0]}: {w[1]}
          </div>
        );
      });
      return (
        <div>
          Your score: {wpm}
          <div>{wrong}</div>
        </div>
      );
    }
    return (
      <div>
        <div>{this.state.currentWord}</div>
        <div>Next: {this.nextWord}</div>
        <div>{this.state.time}</div>
        <form onSubmit={this.makeGuess}>
          <input
            type="text"
            value={this.state.input}
            onChange={this.handleInput}
            autoFocus
          />
        </form>
      </div>
    );
  }
}

export default TypingApp;
