export const arrIsSorted = (arr) => {
    for (let i = 0; i < arr.length - 1; i++) {
        if (arr[i] > arr[i + 1]) {
            return false;
        }
    }
    return true;
};

export const bubbleSort = (array) => {
    let arr = [...array];
    let steps = [];
    for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < arr.length - i - 1; j++) {
            if (arr[j] > arr[j + 1]) {
                steps.push({
                    step: [j, j + 1],
                    swap: true,
                });
                const temp = arr[j];
                arr[j] = arr[j + 1];
                arr[j + 1] = temp;
            } else {
                steps.push({
                    step: [j, j + 1],
                    swap: false,
                });
            }
        }
    }
    return steps;
};

export const selectionSort = (array) => {
    let arr = [...array];
    let steps = [];
    for (let i = 0; i < arr.length; i++) {
        // Finding the smallest number in the subarray
        let min = i;
        for (let j = i + 1; j < arr.length; j++) {
            steps.push({
                step: [j, min],
                swap: false,
            });
            if (arr[j] < arr[min]) {
                min = j;
            }
        }
        if (min !== i) {
            // Swapping the elements
            steps.push({
                step: [i, min],
                swap: true,
            });
            let tmp = arr[i];
            arr[i] = arr[min];
            arr[min] = tmp;
        }
    }
    return steps;
};

const merge = (arr, left, mid, right, steps) => {
    const n1 = mid - left + 1;
    const n2 = right - mid;
    const leftArray = new Array(n1);
    const rightArray = new Array(n2);

    for (let i = 0; i < n1; i++) leftArray[i] = arr[left + i];
    for (let j = 0; j < n2; j++) rightArray[j] = arr[mid + 1 + j];

    let i = 0;
    let j = 0;
    let k = left;

    while (i < n1 && j < n2) {
        if (leftArray[i] <= rightArray[j]) {
            steps.push({
                step: [k, left + i],
                swap: true,
            });
            arr[k] = leftArray[i];
            i++;
        } else {
            steps.push({
                step: [k, j + mid + 1],
                swap: true,
            });
            arr[k] = rightArray[j];
            j++;
        }
        k++;
    }
    while (i < n1) {
        steps.push({
            step: [k, i + left],
            swap: true,
        });
        arr[k] = leftArray[i];
        i++;
        k++;
    }
    while (j < n2) {
        steps.push({
            step: [k, j + mid + 1],
            swap: true,
        });
        arr[k] = rightArray[j];
        j++;
        k++;
    }
};

export const mergeSort = (arr, left, right, steps = []) => {
    if (left >= right) {
        console.log("a", arr);
        return; //returns recursively
    }
    const mid = left + parseInt((right - left) / 2);
    mergeSort(arr, left, mid, steps);
    mergeSort(arr, mid + 1, right, steps);
    merge(arr, left, mid, right, steps);
    console.log("b", arr);
    if (arrIsSorted(arr)) {
        console.log("ccc");
        return steps;
    }
};

