import React from "react";
import { Switch, Route, Link } from "react-router-dom";
import { useHistory } from "react-router";

import TypingApp from "./typingApp/TypingApp";
import SortingVisualizer from "./sortingVisualizer/SortingVisualizer";

import "./App.css";

const Landing = () => {
  return (
    <div className="container">
      <h1 className="mt-5 mb-3 ">My Projects</h1>
      <ul className="list-group">
        <li className="list-group-item">
          <Link to="/typing">Typing App</Link>
        </li>
        <li className="list-group-item">
          <Link to="/sorting">Sorting Visualizer</Link>
        </li>
      </ul>
    </div>
  );
};

const App = () => {
  const history = useHistory();
  return (
    <div>
      <div className="homeBtn" onClick={() => history.push("/")}>
        Home
      </div>
      <Switch>
        <Route exact path="/" component={Landing} />
        <Route exact path="/typing" component={TypingApp} />
        <Route exact path="/sorting" component={SortingVisualizer} />
      </Switch>
    </div>
  );
};

export default App;
